@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans&display=swap);
body {
  font-family: "Alegreya Sans", arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #192a33;
  height: 100vh;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  max-width: 1024px;
  margin: auto;
}

.bets {
  width: 264px;
  height: 416px;
  overflow-y: scroll;
  margin: auto;
  margin-top: 100px;
}

.wrapper {
  width: 264px;
  height: 416px;
  margin: auto;
  position: relative;
}

.wrapper::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 48px;
  bottom: 0;
  margin: 0 auto;
  z-index: 100;
  background: linear-gradient(180deg, rgba(25, 42, 51, 0) 0%, #192a33 100%);
}

.bets::-webkit-scrollbar {
  width: 4px;
}

.bets::-webkit-scrollbar-thumb {
  background: #364d59;
  border-radius: 50px;
}

.bets__header {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  color: #b8ccd9;
  margin-bottom: 8px;
}

.text {
  font-size: 12px;
  line-height: 16px;
  color: #738c99;
}

.bet {
  width: 248px;
  height: auto;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #2a414c;
  margin-bottom: 8px;
  cursor: pointer;
}

.bet__main-info {
  display: flex;
  margin-bottom: 8px;
}

.bet__info {
  width: 48px;
  display: flex;
  flex-direction: column;
  margin-right: 5px;
}

.bet__id {
  text-transform: uppercase;
}

.bet__description {
  display: flex;
  flex-direction: column;
  width: 169px;
}

.bet__event {
  padding-left: 3px;
}

.bet__offside {
  color: #d6b2ff;
  font-weight: bold;
}

.bet__free-kick {
  font-weight: bold;
  color: #a6ddff;
}

.bet__goal-kick {
  font-weight: bold;
  color: #85f2ef;
}

.bet__corner-kick {
  font-weight: bold;
  color: #feb2d5;
}

.bet__goal {
  font-weight: bold;
  color: #ffc6a6;
}

.bet__money-info {
  width: 167px;
  display: flex;
}

.bet__size {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-right: 8px;
}

.bet__paid {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.bet__status {
  display: flex;
  flex-direction: column;
  width: 24px;
  align-items: flex-end;
}

.bet__icon {
  width: 16px;
  height: 16px;
}

.bet__coeff {
  padding-right: 1px;
}

.winning {
  color: #94e107;
}

.additional-info__items {
  display: flex;
  flex-direction: column;
}

.additional-info__item {
  display: flex;
  margin-bottom: 8px;
}

.additional-info__time {
  width: 37px;
  margin-right: 19px;
}

.additional-info__status {
  width: 96px;
  color: #b8ccd9;
}

.additional-info__money {
  width: 40px;
  text-align: right;
  color: #b8ccd9;
}

.additional-info__coeff {
  width: 40px;
  text-align: right;
  padding-right: 3px;
  color: #b8ccd9;
}

.additional-info__icon {
  width: 16px;
  height: 16px;
}

